import { clienteConstants } from "../constants/cliente.constants";

const initialState = {
    cliente: {},
    loading: false,
}

export const clienteReducer = (state = initialState, action) => {
    switch (action.type) {
        case clienteConstants.initialState:
            return {
                ...state,
            }
        case clienteConstants.Create:
            return {
                ...state,
                cliente: action.payload,
            } 
        case clienteConstants.Read:
            return {
                ...state,
                cliente: action.payload
            }
        case clienteConstants.Update:
            return {
                ...state,
                cliente: action.payload
            }
        case clienteConstants.Delete:
            return {
                ...state,
                cliente: {}
            }
        case clienteConstants.startLoading:
            return {
                ...state,
                loading:true
            }
        case clienteConstants.finishLoading:
            return {
                ...state,
                loading: false,
            }    
            
        default:
            return {...state};
    }

}