const path = "api/";
export const fetchToken = (endpoint, data={}, method = 'GET') => {
    const url = `${path}${endpoint}`;
    const token = localStorage.getItem('token') || '';
    if (method === 'GET') {

        return fetch(url, {
            method: method,
            headers: {
                'x-token': token
            }
        });


    } else {
        return fetch(url, {
            method: method,
            headers: {
                'Content-type': 'application/json',
                'x-token': token
            },
            body: JSON.stringify(data)
        })
    }
}
export const fetchNoToken = (endpoint, data, method = 'GET') => {
    const url = `${path}${endpoint}`;
    if (method === 'GET') {
        return fetch(url);
    } else {
        return fetch(url, {
            method: method,
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify(data)
        })
    }
}

