import { Avatar, Button, IconButton, ListItemIcon, ListItemText, Menu, MenuItem, Toolbar, Typography } from '@mui/material'
import { styled } from '@mui/material/styles';
import MuiAppBar from '@mui/material/AppBar';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import LogoutIcon from '@mui/icons-material/Logout';
import HelpCenterIcon from '@mui/icons-material/HelpCenter';
import HelpIcon from '@mui/icons-material/Help';
import React from 'react'
import { uiHideSideBar, uiShowSideBar } from '../../redux/actions/ui.actions';
import { useDispatch, useSelector } from 'react-redux';
import { AvatarUser } from '../avatars/AvatarUser';
import { authLogout } from '../../redux/actions/auth.actions';
const drawerWidth = 280;


const AppBar = styled(MuiAppBar, {
    shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
    transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
    }),
    ...(open && {
        width: `calc(100%)`,
        marginLeft: `${drawerWidth}px`,
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    }),
}));


export const AppBarCustom = ({ appName }) => {

    const dispatch = useDispatch();
    const { sideBar, menuUser } = useSelector(state => state.ui);
    const [anchorEl, setAnchorEl] = React.useState(null);
    const logoutOpen = Boolean(anchorEl);


    const handleClose = () => {
        setAnchorEl(null);
    };
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleDrawerOpen = () => {
        if (sideBar) return dispatch(uiHideSideBar())
        dispatch(uiShowSideBar())
    };

    const handleLogout = () => {
        dispatch(authLogout());
    }

    const handleSupport = () => {
        console.log(" HANDLE SUPPORT");
    }

    return (
        <AppBar position="fixed" color="sideBar" open={sideBar} sx={{ zIndex: (theme) => theme.zIndex.drawer + 1, color: 'white' }}>
            <Toolbar>
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    onClick={handleDrawerOpen}
                    edge="start"
                    sx={{ mr: 2, ...(false && { display: 'none' }) }}
                >
                    {!sideBar && <MenuIcon />}
                    {sideBar && <CloseIcon />}
                </IconButton>
                <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                    {appName}
                </Typography>
                <Button
                    variant="primary"
                    id="basic-button"
                    aria-controls={logoutOpen ? 'basic-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={logoutOpen ? 'true' : undefined}
                    onClick={handleClick}
                >
                    <AvatarUser />
                </Button>
                <Menu
                    id="basic-menu"
                    anchorEl={anchorEl}
                    open={logoutOpen}
                    onClose={handleClose}
                    MenuListProps={{
                        'aria-labelledby': 'basic-button',
                    }}
                >
                    <MenuItem onClick={handleLogout}>
                        <ListItemIcon>
                            <LogoutIcon fontSize="small" />
                        </ListItemIcon>
                        <ListItemText>Cerrar Sesion</ListItemText>
                    </MenuItem>
                    <MenuItem onClick={handleLogout}>
                        <ListItemIcon>
                            <HelpIcon fontSize="small" />
                        </ListItemIcon>
                        <ListItemText>Support</ListItemText>
                    </MenuItem>
                </Menu>
            </Toolbar>
        </AppBar>
    )
}
