import {applyMiddleware, combineReducers, compose, createStore} from 'redux'
import thunk from 'redux-thunk';
import { authReducer } from './reducers/auth.reducers';
import { clienteReducer } from './reducers/cliente.reducers';
import { formReducer } from './reducers/form.reducers';
import { PrestamoReducer } from './reducers/prestamo.reducers';
import { uiReducer } from './reducers/ui.reducers';

const reducers = combineReducers({
    ui:uiReducer,
    auth:authReducer,
    form:formReducer,
    cliente:clienteReducer,
    prestamo:PrestamoReducer
});


const composeEnhancers = (typeof window !== 'undefined' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__) || compose;


export const store = createStore(
    reducers,
    composeEnhancers(
        applyMiddleware(thunk)
    )
);