import { uiConstants } from "../constants/ui.constanst"

const { uiOpenModal, uiCloseModal, uiOpenLoading, uiCloseLoading,
    uiHideSideBar, uiShowSideBar, uiErrorMessage,
    uiShowMenuUser, uiHideMenuUser, uiShowQuickSearch, uiHideQuickSearch, uiSearchedDataQuickSearch, uiCleanQuickSearch, uiSelectedQuickSearch, uiSelectOption,uiQuickSearchId } = uiConstants;

const initialState = {
    modalOpen: false,
    loading: false,
    sideBar: false,
    optionSelected: '',
    menuUser: false,
    quickSearch: false,
    quickSearchData: [],
    quickSearchSelected: null,
    quickSearchId: null,
    error: null,
}
export const uiReducer = (state = initialState, action) => {

    switch (action.type) {
        case uiOpenModal:
            return {
                ...state,
                modalOpen: true
            }
        case uiCloseModal:
            return {
                ...state,
                modalOpen: false
            }
        case uiOpenLoading:
            return {
                ...state,
                loading: true
            }

        case uiCloseLoading:
            return {
                ...state,
                loading: false
            }
        case uiHideSideBar:
            return {
                ...state,
                sideBar: false
            }
        case uiShowSideBar:
            return {
                ...state,
                sideBar: true
            }
        case uiShowMenuUser:
            return {
                menuUser: true
            }
        case uiHideMenuUser:
            return {
                menuUser: false
            }
        case uiShowQuickSearch:
            return {
                ...state,
                quickSearch: true,
                quickSearchData: [],
                quickSearchSelected:null,
            }
        case uiHideQuickSearch:
            return {
                ...state,
                quickSearch: false,
                quickSearchData: []
            }
        case uiSearchedDataQuickSearch:
            return {
                ...state,
                quickSearchData: action.payload
            }
        case uiSelectedQuickSearch:
            return {
                ...state,
                quickSearchSelected: action.payload
            }
        case uiCleanQuickSearch:
            return {
                ...state,
                quickSearchSelected:null
            }    
        case uiQuickSearchId:
            return {
                ...state,
                quickSearchId: action.payload
            }  
        case uiErrorMessage:
            return {
                ...state,
                error: action.payload
            }

        case uiSelectOption:
            return {
                ...state,
                optionSelected: action.payload
            }
        default:
            return state;
    }
}