import React, { lazy, Suspense, useEffect } from 'react';
import { Routes, Route, Navigate } from "react-router-dom";
import { Spinner } from '../components/spinner/Spinner';
import { useNavigate } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import { authChecking } from '../redux/actions/auth.actions';

const LazyLogin = lazy(() => import('../Pages/auth/LoginPage'));
const LazyRegister = lazy(() => import('../Pages/auth/RegisterPage'));


export const AuthRouter = () => {

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { uid, checking } = useSelector(state => state.auth)

  useEffect(() => {
    dispatch(authChecking())
  }, [dispatch])

  useEffect(() => {
    if (uid) navigate("/home")
  }, [uid]);
  


  return (
    <>
    
      <div>
        <Suspense fallback={<Spinner />}>
          <Routes>
            <Route path='/login' element={<LazyLogin />} />
            <Route path='/register' element={<LazyRegister />} />
            <Route path="/*" element={<Navigate to='/login' replace />} />
          </Routes>
        </Suspense>
      </div>

    </>
  )
};
