import { fetchNoToken, fetchToken } from "../../helpers/fetch";
import { authConstants } from "../constants/auth.constants"
import { uiConstants } from "../constants/ui.constanst"

export const authCreateRegister = ({ name, lastName, email, password }) => {
    return async (dispatch) => {
        try {
            dispatch({ type: uiConstants.uiOpenLoading });
            const resp = await fetchNoToken('auth/new', { name, lastName, email, password }, 'POST');
            const body = await resp.json();
            if (body.ok) {
                localStorage.setItem('token', body.token);
                localStorage.setItem('token-init-date', new Date().getTime());
                dispatch({ type: authConstants.authLogin, payload: { uid: body.uid, name: body.name, lastName: body.lastName } })
                dispatch({ type: uiConstants.uiErrorMessage, payload: null })
            }
            else {
                dispatch({ type: uiConstants.uiErrorMessage, payload: body.msg});
            }
            dispatch({ type: uiConstants.uiCloseLoading });
        } catch (error) {
            console.error(error);
            dispatch({ type: uiConstants.uiCloseLoading });
            dispatch({ type: uiConstants.uiErrorMessage, payload: JSON.stringify(error) })
        }

    }
}

export const authChecking = () => {
    return async (dispatch) => {
        const resp = await fetchToken(`auth/renew`, {}, 'GET');
        const body = await resp.json();
        if (body.ok) {
            localStorage.setItem('token', body.token);
            localStorage.setItem('token-init-date', new Date().getTime());
            dispatch({ type: authConstants.authLogin, payload: { name: body.name, uid: body.uid,lastName: body.lastName, email: body.email } })
        } else {
            // dispatch({ type: authConstants.authChecking })
        }
    }
}


export const authLogin =({email,password})=>{
    return async (dispatch) => {
        try {
            dispatch({ type: uiConstants.uiOpenLoading });
            const resp = await fetchNoToken('auth/login',{email, password},'POST');
            const body = await resp.json();
            if (body.ok) {
                localStorage.setItem('token', body.token);
                localStorage.setItem('token-init-date', new Date().getTime());
                dispatch({ type: authConstants.authLogin, payload: { uid: body.uid, name: body.name, lastName: body.lastName } })
                dispatch({ type: uiConstants.uiErrorMessage, payload: null })
            }
            else {
                dispatch({ type: uiConstants.uiErrorMessage, payload: body.msg });
            }
            dispatch({ type: uiConstants.uiCloseLoading });
            
        } catch (error) {
            console.error(error);
            dispatch({ type: uiConstants.uiCloseLoading });
            dispatch({ type: uiConstants.uiErrorMessage, payload: JSON.stringify(error) })
        }
    }
}

export const authLogout =() => {
    return async (dispatch) => {
        localStorage.removeItem('token');
        dispatch({type: authConstants.authLogout})
    }
}