import { ExpandLess, ExpandMore } from '@mui/icons-material'
import { Collapse, Divider, ListItemButton, ListItemIcon, ListItemText } from '@mui/material'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router'
import { theme } from '../../configtheme'
import useCheckMobileScreen from '../../hooks/useCheckMobileScreen'
import { uiHideSideBar, uiSelectOption } from '../../redux/actions/ui.actions'
import './sidebar.css'

export const SidebarItem = ({ item, nivel,child=false }) => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const isMobile = useCheckMobileScreen();

    const {optionSelected} = useSelector(state=> state.ui);

    const { url, name, items, icon } = item
    const [collapsed, setCollapsed] = React.useState(false);
    const [isSelected, setSelected] = React.useState(false)
    const handleCollapse = () => {

        if (items.length > 0) {
            setCollapsed(prevValue => !prevValue)
            setSelected(prevValue => !prevValue);
        }
        else {
            navigate(url);
            dispatch(uiSelectOption(name))
            if (isMobile) {
                dispatch(uiHideSideBar())
            }
        }
    }
    return (
        <div style={{ marginTop: child ?'0px':'10px'}}>
            <ListItemButton  className={optionSelected === name?'item-selected':''} onClick={() => handleCollapse()} key={item.id} style={{
                 border: `2px solid ${theme.palette.valen.strong}`, borderRadius: 10,
            }}>
                <ListItemIcon  style={{ width: '10px !important' }}>
                    {icon !== '' && icon}
                </ListItemIcon>
                <ListItemText primary={name} />
                {
                    (items.length > 0 && !isSelected) && <ExpandMore />
                }
                {
                    (items.length > 0 && isSelected) && <ExpandLess />
                }
            </ListItemButton>
            <Collapse in={collapsed} timeout="auto" unmountOnExit>
                {
                    items &&
                    items.map((el, ix) => (
                        <SidebarItem child={true} nivel={nivel * 2} item={el} key={ix} />
                    ))
                }
            </Collapse>
        </div>
    )
}
