import React, { lazy, Suspense, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Routes, Route, Navigate } from "react-router-dom";
import { Spinner } from '../components/spinner/Spinner';
import { menuRouters } from './Routes';
import { useNavigate } from 'react-router';
import { authChecking } from '../redux/actions/auth.actions';


export const DashboardRouter = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { uid, checking } = useSelector(state => state.auth)

  useEffect(() => {
    dispatch(authChecking())
  }, [dispatch])

  useEffect(() => {
    if (!uid) navigate("/login")
  }, [uid]);

  if (checking) {
    return <Spinner></Spinner>
  }

  return (
    <>
      <div>
        <Suspense fallback={<Spinner />}>
          <Routes>
            {menuRouters.filter(menu => menu.url).map((menu) => (
              <Route key={menu.url} path={menu.url} element={<menu.Component />} />
            ))}
            <Route path="/*" element={<Navigate to='/home'  />} />
          </Routes>
        </Suspense>

      </div>

    </>
  )
};
