import styled from '@emotion/styled';
import { Box, Card, circularProgressClasses, Container, CssBaseline, Paper, Toolbar } from '@mui/material';
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Outlet } from 'react-router';
import useCheckMobileScreen from '../../hooks/useCheckMobileScreen';
import { uiHideSideBar, uiShowSideBar } from '../../redux/actions/ui.actions';
import { DashboardRouter } from '../../Routers/DashboardRouter';
import { AppBarCustom } from '../appbar/AppBarCustom';
import { Footer } from '../footers/Footer';
import Sidebar from '../sidebar/Sidebar';
import { SidebarHeader } from '../sidebar/SidebarHeader';



const drawerWidth = 260;

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' && prop !== 'ismobile' })(
    ({ theme, open, ismobile }) => ({
        visibility: `${(open && ismobile) ? 'hidden' : 'visible'}`,
        flexGrow: 1,
        padding: theme.spacing(3),
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        marginLeft: `-${drawerWidth}px`,
        ...(open && {
            transition: theme.transitions.create('margin', {
                easing: theme.transitions.easing.easeOut,
                duration: theme.transitions.duration.enteringScreen,
            }),
            marginLeft: 0,
        }),
    }),
);




export const Dashboard = () => {
    const { sideBar } = useSelector(state => state.ui);
    const isMobile = useCheckMobileScreen();

    return (
        <Box style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '100vh' }}>
            <CssBaseline />
            <AppBarCustom appName="GoCredix" />
            <Sidebar drawerWidth={drawerWidth} />
            <Main open={sideBar} ismobile={isMobile} style={{overflow:'auto'}} >
                <Container component="main" maxWidth="lg" sx={{ mb: 3 }}>
                    <Paper elevation={4} sx={{ my: { xs: 3, md: 1 }, p: { xs: 2, md: 3 } }}>
                        <DashboardRouter>
                            <Outlet />
                        </DashboardRouter>
                    </Paper>
                </Container>
            </Main>
            {/* <Footer /> */}
        </Box>
    )
}
