import { formConstants } from "../constants/form.constants";

const initialState = {
    initial: true,
    newE: true,
    disabled: true,
    search: true,
    save: false,
    canDelete: false,
    cancel: false,
    done: false,
    msje: ''
}

const { formInitial, formNew, formCancel, formSave, formSearch, formDelete,formFetched } = formConstants

////REDUCER ENCARGADO DE MANEJAR LOS ESTADOS DE APARICIONES DE LOS BOTONES DE LA "BARRA"

export const formReducer = (state = initialState, action) => {
    switch (action.type) {
        case formInitial:
            return {
                initial: true,
                newE: true,
                disabled: true,
                search: true,
                save: false,
                canDelete: false,
                cancel: false,
                done: false,
                msje: ''
            }
        case formNew:
            return {
                ...state,
                disabled: false,
                newE: false,
                cancel: true,
                search: false,
                save: true,
                canDelete: false,
                msje: ''
            }
        case formCancel:
            return {
                ...state,
                disabled: true,
                newE: true,
                cancel: false,
                save: false,
                search: true,
                canDelete: false,
                msje: ''
            }
        case formSave:
            return {
                ...state,
                disabled: true,
                newE: true,
                cancel: false,
                save: false,
                done: true,
                canDelete: false,
                msje: 'Saved'
            }
        case formDelete:
            return {
                ...state,
                disabled: true,
                newE: true,
                cancel: false,
                done: true,
                msje: 'Deleted',
                canDelete: false,
                save:false
            }
        case formSearch:
            return {
                ...state,
                newE: true,
                cancel: false,
                save: true,
                search:true,
                canDelete: false,

            }
        case formFetched:{
            return {
                ...state,
                disabled: false,
                save:true,
                canDelete:true
            }
        }    
        default:
            return state;
    }
}