import { authConstants } from "../constants/auth.constants";

const { authLogin, authRegister, authLogout, authChecking, authRenew, authSendEmail, authChangePassword } = authConstants;

const initialState = {
    uid: null,
    name: null,
    email:null,
    code: null,
    emailSent: null,
    checking: true,
};

export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case authLogin:
            return {
                ...state,
                checking: false,
                uid: action.payload.uid,
                name: action.payload.name.trim() +" "+action.payload.lastName.trim(),
                email: action.payload.email
            }
        case authRegister:
            return {
                ...state,
                uid: action.payload.uid,
                name: action.payload.name +" "+action.payload.lastName,
                email: action.payload.email
            }
        case authLogout:
            return {
                ...state,
                checking: false,
                uid: null,
                name: null,
                email: null,
            }
        case authChecking:
            return {
                ...state,
                checking: false,
            }
        case authRenew:
            return { ...state, }
        case authSendEmail:
            return { ...state, }
        case authChangePassword:
            return { ...state, }
        default:
            return { ...state }
    }
}